import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import {selectFriends} from '../../store/reducers';
import {frendsActions} from '../../store/actions';

import {Panel,Group} from '@vkontakte/vkui';
import HeaderPanel from '../../components/HeaderPanel';
import ScreenSpinner from '../../components/ScreenSpinner/ScreenSpinner';

import { LineChart, Line, PieChart, Pie, Sector, Cell, Legend } from 'recharts';

import db from './db_friends.json';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#FF80333', '#FA8542'];
const style = {
	top: 0,
	left: '3px',
	lineHeight: '24px'
};

const renderCustomizedLabel = ({ cx, cy, midAngle, innerRadius, outerRadius, percent, index }) => {
	const RADIAN = Math.PI / 180;
	const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
	const x  = cx + radius * Math.cos(-midAngle * RADIAN);
	const y = cy  + radius * Math.sin(-midAngle * RADIAN);

	return (<text x={x} y={y} fill="white" textAnchor={x > cx ? 'start' : 'end'} dominantBaseline="central" />);
};

class City extends Component {

	state ={
		screenSpinner: true
	};

	constructor(props) {
		super(props);
	}

	componentWillMount() {
		const {dispatch, users} = this.props;
		const {fetchedAllFriends} = frendsActions;
		if (!users.length){
			dispatch(fetchedAllFriends(null, this.ending.bind(this)));
		} else {
			this.ending();
		}
	}

	ending () {
		this.setState({ screenSpinner: false });
	}

	render () {
		const {id, go, users} = this.props;
		const {screenSpinner} = this.state;
		return (
			<Panel id={id}>
				<HeaderPanel title='Города' handelClick={go} dataTo='home' />
				{screenSpinner ? 
					<ScreenSpinner /> :
					<Group>
						<PieChart width={320} height={500} onMouseEnter={this.onPieEnter}>
							<Pie
								dataKey='value'
								data={users} 
								cx="70%" 
								cy="30%" 
								labelLine={false}
								label={renderCustomizedLabel}
								outerRadius={80} 
								fill="#8884d8"
							>
								{users && users.map((entry, index) => <Cell key={index} fill={COLORS[index % COLORS.length]}/>)}
							</Pie>
							<Legend 
								iconSize={10}
								iconType='circle'
								width={320}
								height={5}
								layout='vertical'
								verticalAlign='middle'
								wrapperStyle={style}/>
						</PieChart>
					</Group>}
			</Panel>
		);
	}
}

City.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

const mapStateToProps = (state) => ({
	users: selectFriends.getSortFriendsFromCity(state)
});
export default connect(mapStateToProps, null)(City);