import React from 'react';
import PropTypes from 'prop-types';

import {classNames, getClassName, PopoutWrapper, Spinner, } from '@vkontakte/vkui';
import './ScreenSpinner.css';

const baseClassName = getClassName('ScreenSpinner');

const ScreenSpinner = ({ ...props }) => (
	<PopoutWrapper className={classNames(baseClassName, 'ScreenSpinner__disabled')} >
		<div className="ScreenSpinner__container">
			<Spinner size="large" {...props} />
		</div>
	</PopoutWrapper>
);

export default ScreenSpinner;