import React from 'react';
import PropTypes from 'prop-types';

import {Panel, Avatar} from '@vkontakte/vkui';
import Icon24User from '@vkontakte/icons/dist/24/back';

import'./Story.css';

const Storiy = ({id, size=64, name, url=''}) => {
	const classesStoryUser = ['Story__user-avatar']
	const handleClick = () => {
		classesStoryUser.push('Story__is-viewed');
	};

	return(
		<div key={id} className='Story__user' onClick={handleClick} >
			<Avatar
				className={classesStoryUser.join(' ')}
				size={size}
				src={url}><Icon24User /></Avatar>
			<div className='Stories__full-name'>{name}</div>
		</div>
	);
}

Storiy.propTypes = {
};

export default Storiy;