import {combineReducers} from 'redux';
import user, * as selectUser from './user/reducer';
import friends, * as selectFriends from './friends/reducer';
import search, * as selectSearch from './search/reducer';
import stories, * as selectStories from './stories/reducer';

export default combineReducers({
	user,
	friends,
	search,
	stories
});

export {
	selectUser,
	selectFriends,
	selectSearch,
	selectStories
}