import {
	FETCHED_FRIENDS,
	UPDATE_FRIENDS
} from './actionType';

const userInitialState = {
	users: []
};


export default function friends (state = userInitialState, action = {}) {
	switch (action.type) {
		case FETCHED_FRIENDS:
			return {
				...state,
				offsetFriends: action.offsetFriends,
				users: [...action.users],
			};
		case UPDATE_FRIENDS:
			return {
				...state,
				offsetFriends: action.offsetFriends,
				users: [...state.users, ...action.users],
			};
		default:
			return state;
	};
};

export const getFriends = (state) => state.friends.users;
export const getFriendsInInstagram = (state) => state.friends.users.filter(user => user.instagram);
export const getBannedFriends = (state) => state.friends.users.filter(user => user.deactivated === 'deleted' || user.deactivated === 'banned');
export const getSortLongTime = (state) => {
	return state.friends.users.sort((a,b) => {
		return 	a.last_seen &&
				b.last_seen &&
				a.last_seen.time &&
				b.last_seen.time && (a.last_seen.time - b.last_seen.time)
	});
};

export const getSortFriendsFromCity = (state) => {
	const friends = (users=[]) => {
		const friends = {};
		users.map((friend) => {
			if (friend.city){
				if (friends[friend.city.id]) {
					friends[friend.city.id].value += 1;
					friends[friend.city.id].name = friend.city.title;
				} else {
					friends[friend.city.id] = {
						id: friend.city.id,
						name: `${friend.city.title}`,
						value: 1
					};
				}
			}
		});
		return friends;
	};
	const friendsSort = (users={}) => {
		const friendsSort = {};
		Object.values(users).map((friend) => {
			if (friend.value < 10) {
				if (!friendsSort['others']){
					friendsSort['others'] = {
						value: 0,
						items: [],
						name: ''
					};
				}
				friendsSort['others'].items.push(friend);
				friendsSort['others'].value += 1;
				friendsSort['others'].name = `others - ${friendsSort['others'].value}`;
			} else {
				if (!friendsSort[friend.id]) {
					friendsSort[friend.id] = {
						value: 0,
						items: [],
						name: ''
					};
				}
				friendsSort[friend.id].items.push(friend);
				friendsSort[friend.id].value += friend.value;
				friendsSort[friend.id].name = `${friend.name} - ${friendsSort[friend.id].value}`;
			}
		});
		return Object.values(friendsSort).sort((a,b) => b.value - a.value);
	};
	return friendsSort(friends(state.friends.users));
};