import { SET_USER, GET_USERS_PROFILE } from './actionType';

const userInitialState = {
	user: {
		photo_200: 'https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg',
		first_name: 'Вася',
		last_name: 'Васечкин',
		city: { title: 'New York' }
	},
	profile: []
};

export default function users (state = userInitialState, action = {}) {
	switch (action.type) {
		case SET_USER:
			return {
				...state,
				user: {...action.payload}
			};
		case GET_USERS_PROFILE:
			return {
				...state,
				usersProfile: [...action.payload]
			};
		default:
			return state;
	};
};

export const getUser = (state) => {
	return state.user.user;
};

export const getUsersProfile = (state) => {
	return state.user.profile;
};