import {SET_USER, GET_USERS_PROFILE} from './actionType';
import {getUser} from '../../utils/api/vk';

export const fetchedUser = () => async (dispatch) => {
	const data = await getUser({});
	dispatch({type: SET_USER, payload: data});
};

export const fetchedUsersProfile = ({user_ids}) => async (dispatch) => {
	const data = await getUser({user_ids});
	dispatch({type: GET_USERS_PROFILE, payload: data});
};